import { Component } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { apiResultFormat } from 'src/app/core/core.index';
import { routes } from 'src/app/core/helpers/routes';
import { DataService } from 'src/app/core/service/data/data.service';
import { SidebarService } from 'src/app/core/service/sidebar/sidebar.service';
import { PaginationService, pageSelection, tablePageSize } from 'src/app/shared/custom-pagination/pagination.service';
import { subcategories } from 'src/app/shared/model/page.model';
import Swal from 'sweetalert2';
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
  FormArray
} from '@angular/forms';
import { dA } from '@fullcalendar/core/internal-common';
interface data {
  value: string;
}

declare var bootstrap: any;

@Component({
  selector: 'app-sub-categories',
  templateUrl: './sub-categories.component.html',
  styleUrl: './sub-categories.component.scss'
})
export class SubCategoriesComponent {
  public routes = routes;
  initChecked = false;
  // pagination variables
  public tableData: Array<subcategories> = [];
  public pageSize = 10;
  public serialNumberArray: Array<number> = [];
  public totalData = 0;
  showFilter = false;
  dataSource!: MatTableDataSource<subcategories>;
  public searchDataValue = '';
  public categoryData: { category_id: number; name: string; is_active: boolean }[] = [];
  subCategoryForm!: FormGroup;
  updateBasicForm!: FormGroup;
  updateConditionsForm!: FormGroup;
  conditionsArray!: FormArray;
  //** / pagination variables

  constructor(
    private data: DataService,
    private pagination: PaginationService,
    private router: Router,
    private sidebar: SidebarService,
    private fb: FormBuilder
  ) {
    this.subCategoryForm = this.fb.group({
      name: ['', Validators.required],
      category_id: ['', Validators.required],
      condition_types: this.fb.array([]) // Dynamic condition types
    });

    this.updateBasicForm = this.fb.group({
      subcategory_id: ['', Validators.required],
      name: ['', Validators.required],
      is_active: [true, Validators.required]
    });

    // Second Form (Conditions)
    this.updateConditionsForm = this.fb.group({
      subcategory_id: ['', Validators.required],
      conditions: this.fb.array([]) // Dynamic conditions
    });

    this.conditionsArray = this.updateConditionsForm.get('conditions') as FormArray;

    this.data.getDataTable().subscribe((apiRes: apiResultFormat) => {
      this.totalData = apiRes.totalData;
      this.pagination.tablePageSize.subscribe((res: tablePageSize) => {
        if (this.router.url == this.routes.subCategories) {
          this.getTableData();
          this.pageSize = res.pageSize;
        }
      });
    });

    this.data.getCategoryList().subscribe((apiRes: apiResultFormat) => {
      this.categoryData = apiRes.data;
    });
  }
  editSubCategory(data: any): void {
      // Populate Basic Form
      this.updateBasicForm.patchValue({
        subcategory_id: data.subcategory_id,
        name: data.name,
        is_active: data.is_active
      });
      console.log(data)
      // Populate Conditions Form
      this.updateConditionsForm.patchValue({
        subcategory_id: data.subcategory_id
      });
      // Clear conditions array and populate with existing conditions
      this.conditionsArray.clear();
      if (data.conditions && Array.isArray(data.conditions)) {
        data.conditions.forEach((condition: { condition_id:number ,condition: string; points: number; is_active: boolean }) => {
          this.conditionsArray.push(this.fb.group({
            condition_id: [condition.condition_id, Validators.required],
            condition: [condition.condition, Validators.required],
            points: [condition.points, Validators.required],
            is_active: [condition.is_active]
          }));
        });
      }      
  }
  updateBasicSubcategory() {
    if (this.updateBasicForm.valid) {
      this.data.updateSubCategory(this.updateBasicForm.value).subscribe(
        (response) => {
          this.getTableData();
          // // Reset form after submission
          this.updateBasicForm.reset();
          // Close the modal using Bootstrap's API
          const modalElement = document.getElementById('edit-category');
          if (modalElement) {
            const modal = bootstrap.Modal.getInstance(modalElement);
            modal.hide();
          }
        },
        (error) => {
          console.error('Error creating user', error);
        }
      );
    }
  }

  // Submit Second Form (Update Conditions)
  updateSubcategoryConditions() {
    if (this.updateConditionsForm.valid) {
      this.data.updateSubCategoryConditions(this.updateConditionsForm.value).subscribe(
        (response) => {
          this.getTableData();
          // // Reset form after submission
          // this.updateBasicForm.reset();
          // // Close the modal using Bootstrap's API
          // const modalElement = document.getElementById('edit-category');
          // if (modalElement) {
          //   const modal = bootstrap.Modal.getInstance(modalElement);
          //   modal.hide();
          // }
        },
        (error) => {
          console.error('Error creating user', error);
        }
      );
    }
  }
  private getTableData(): void {
    this.data.getSubCategories().subscribe((apiRes: apiResultFormat) => {
          this.tableData = [];
          apiRes.data.forEach((res: subcategories, index: number) => {
            // Push data to the table array
            this.tableData.push({
              ...res,
            });
          });
        });
  }
  get conditionTypes(): FormArray {
    return this.subCategoryForm.get('condition_types') as FormArray;
  }
  addCondition() {
    this.conditionTypes.push(this.fb.group({
      condition_id: Date.now() + Math.floor(Math.random() * 1000),  
      condition: ['', Validators.required],
      points: [0, Validators.required],
      is_active: [true]
    }));
  }

  removeCondition(index: number) {
    this.conditionTypes.removeAt(index);
  }
  addEditCondition() {
    this.conditionsArray.push(this.fb.group({
      condition_id: Date.now() + Math.floor(Math.random() * 1000),
      condition: ['', Validators.required],
      points: [0, Validators.required],
      is_active: [true]
    }));
  }

  removeEditCondition(index: number) {
    this.conditionsArray.removeAt(index);
  }


  submitForm() {
    if (this.subCategoryForm.valid) {
        this.data.createSubCategory(this.subCategoryForm.value).subscribe(
          (response) => {
            this.getTableData();
            // Reset form after submission
            this.subCategoryForm.reset();
            // Close the modal using Bootstrap's API
            const modalElement = document.getElementById('add-category');
            if (modalElement) {
              const modal = bootstrap.Modal.getInstance(modalElement);
              modal.hide();
            }
          },
          (error) => {
            console.error('Error creating user', error);
          }
        );
      }
  }
  public sortData(sort: Sort) {
    const data = this.tableData.slice();
    if (!sort.active || sort.direction === '') {
      this.tableData = data;
    } else {
      this.tableData = data.sort((a, b) => {
        const aValue = (a as never)[sort.active];
        const bValue = (b as never)[sort.active];
        return (aValue < bValue ? -1 : 1) * (sort.direction === 'asc' ? 1 : -1);
      });
    }
  }

  public searchData(value: string): void {
    this.dataSource.filter = value.trim().toLowerCase();
    this.tableData = this.dataSource.filteredData;
  }

  confirmColor() {
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: ' btn btn-success',
        cancelButton: 'me-2 btn btn-danger',
      },
      buttonsStyling: false,
    });

    swalWithBootstrapButtons
      .fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        confirmButtonText: 'Yes, delete it!',
        showCancelButton: true,
        cancelButtonText: 'Cancel',
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          swalWithBootstrapButtons.fire(
            'Deleted!',
            'Your file has been deleted.',
            'success'
          );
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          swalWithBootstrapButtons.fire(
            'Cancelled',
            'Your imaginary file is safe :)',
            'error'
          );
        }
      });
  }

  public selectedValue1 = '';
  public selectedValue2 = '';
  public selectedValue3 = '';
  public selectedValue4 = '';
  public selectedValue5 = '';
  public selectedValue6 = '';

  selectedList1: data[] = [
    { value: 'Sort by Date' },
    { value: 'Newest' },
    { value: 'Oldest' },
  ];
  selectedList2: data[] = [
    { value: 'Choose Category' },
    { value: 'Laptop' },
    { value: 'Electronics' },
    { value: 'Shoe' },
  ];
  selectedList3: data[] = [
    { value: 'Choose SubCategory' },
    { value: 'Fruits' },
    
  ];
  selectedList4: data[] = [
    { value: 'Category Code' },
    { value: 'CT001' },
    { value: 'CT002' },
    
  ];
  selectedList5: data[] = [
    { value: 'Choose Category' },
    { value: 'Category' },
  ];
  selectedList6: data[] = [
    { value: 'Computers' },
    { value: 'Fruits' },
  ];

  public filter = false;
  openFilter() {
    this.filter = !this.filter;
  }
  isCollapsed: boolean = false;
  toggleCollapse() {
    this.sidebar.toggleCollapse();
    this.isCollapsed = !this.isCollapsed;
  }
  selectAll(initChecked: boolean) {
    if (!initChecked) {
      this.tableData.forEach((f) => {
        f.isSelected = true;
      });
    } else {
      this.tableData.forEach((f) => {
        f.isSelected = false;
      });
    }
  }
}
